import { setGlobalAccessToken } from "modules/login/context/accessToken";
import { AuthContext } from "modules/login/context/authContext";
import { refreshAdmin } from "modules/login/loginFetch";
import { TCUser } from "modules/login/loginType";
import { deleteRefreshToken, getRefreshToken } from "modules/login/token";
import { Header } from "other/global/header/header";
import { RouteGenerator } from "other/router/routeGenerator/route-generator";
import { admin, auth, mutual, superAdmin, unAuth } from "other/router/routes";
import NotFound from "pages/notFound";
import { useEffect, useState } from "react";
import { useMutation } from "react-query";
import { generatePath, Navigate, Route, Routes } from "react-router-dom";
export type tokenInfo = TCUser;
function App() {
  const [checkAuth, setCheckAuth] = useState<string | null>(getRefreshToken());

  const [userInfo, setUserInfo] = useState<tokenInfo | undefined>();

  const refreshToken = getRefreshToken() ?? "null";
  const refreshObject = { token: refreshToken };

  const $refresh = useMutation(refreshAdmin);

  useEffect(() => {
    if (window.location.pathname !== "/login/success") {
      $refresh.mutate(refreshObject, {
        onSuccess: (data) => {
          data.accessToken
            ? setGlobalAccessToken(data.accessToken)
            : setGlobalAccessToken(null);
          setUserInfo({
            _id: data.user._id,
            name: data.user.name,
            email: data.user.email,
            picture: data.user.picture,
            role: data.user.role,
          });
        },
        onError: () => {
          const urlPath = window.location.pathname.split("/")[1];
          if (urlPath !== "quiz") {
            deleteRefreshToken();
            setGlobalAccessToken(null);
            setCheckAuth(null);
            setUserInfo(undefined);
          }
        },
      });
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const mutualRoutes =
    userInfo?.role !== "admin" &&
    userInfo?.role !== "Super" &&
    RouteGenerator(mutual);

  const authRoutes =
    checkAuth && userInfo?.role === "user" ? RouteGenerator(auth) : null;
  const AdminRoutes =
    checkAuth && userInfo?.role === "admin" ? RouteGenerator(admin) : null;
  const SuperRoutes =
    checkAuth && userInfo?.role === "Super" ? RouteGenerator(superAdmin) : null;
  const unAuthRoutes = !checkAuth ? RouteGenerator(unAuth) : null;

  return (
    <>
      <AuthContext.Provider
        value={{ checkAuth, userInfo, setCheckAuth, setUserInfo }}
      >
        {checkAuth && <Header />}
        <Routes>
          {!$refresh.isLoading && (
            <>
              {AdminRoutes}
              {authRoutes}
              {unAuthRoutes}
              {SuperRoutes}
              {checkAuth && userInfo?.role === "user" && (
                <Route
                  path="*"
                  element={<Navigate to={generatePath("/main")} />}
                />
              )}
              {checkAuth && userInfo?.role === "admin" && (
                <Route
                  path="*"
                  element={
                    <Navigate to={generatePath("/admin/transactions")} />
                  }
                />
              )}
              {checkAuth && userInfo?.role === "Super" && (
                <Route
                  path="*"
                  element={<Navigate to={generatePath("/adminlist")} />}
                />
              )}
              {mutualRoutes}
              {!checkAuth && (
                <Route path="*" element={<Navigate to={generatePath("/")} />} />
              )}
              <Route path="*" element={<NotFound />} />
            </>
          )}
        </Routes>
      </AuthContext.Provider>
    </>
  );
}

export default App;
