import { tokenInfo } from "App";
import { createContext, useContext } from "react";

export type AuthContent = {
  checkAuth?: string | null;
  userInfo?: tokenInfo;
  setCheckAuth: React.Dispatch<React.SetStateAction<string | null>>;
  setUserInfo: React.Dispatch<React.SetStateAction<tokenInfo | undefined>>;
};
export const AuthContext = createContext<AuthContent>({
  checkAuth: "",
  userInfo: {
    name: "",
    email: "",
    picture: "",
    role: "",
    _id: "",
  },
  setCheckAuth: () => null,
  setUserInfo: () => {},
});
export const useAuthContext = () => useContext(AuthContext);
