import { TRegistration } from "modules/registration/registrationType";
import { Request } from "other/request";

import { TAuth, TGetUser, TRefresh } from "./loginType";

export type AuthInputs = {
  email: string;
  password: string;
};
export const auth = async ({ email, password }: AuthInputs) =>
  Request<TRegistration>("/auth/login", "POST", {
    email,
    password,
  });
export const refresh = async () => Request<TAuth>("/protected", "GET", null);
export const refreshAdmin = async (refreshObject: { token: string }) =>
  Request<TRefresh>("/auth/refresh", "POST", refreshObject);
export const logout = async (refreshObject: { refreshToken: string }) =>
  Request<TAuth>("/auth/logout", "DELETE", refreshObject);

export const getUser = async () => Request<TGetUser>("/users", "GET", null);
