import MenuIcon from "@mui/icons-material/Menu";
import { ButtonGroup } from "@mui/material";
import AppBar from "@mui/material/AppBar";
import Avatar from "@mui/material/Avatar";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Container from "@mui/material/Container";
import IconButton from "@mui/material/IconButton";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import Toolbar from "@mui/material/Toolbar";
import Tooltip from "@mui/material/Tooltip";
import Typography from "@mui/material/Typography";
import { Stack } from "@mui/system";
import { useAuthContext } from "modules/login/context/authContext";
import { logout } from "modules/login/loginFetch";
import { deleteRefreshToken, getRefreshToken } from "modules/login/token";
import * as React from "react";
import { useTranslation } from "react-i18next";
import { useMutation } from "react-query";
import { useNavigate } from "react-router";
export const Header = () => {
  const { checkAuth, userInfo, setCheckAuth, setUserInfo } = useAuthContext();

  const $logout = useMutation((refreshObject: { refreshToken: string }) =>
    logout(refreshObject)
  );
  const navigate = useNavigate();
  const logoutFucn = () => {
    const refreshToken = getRefreshToken() ?? "null";
    const refreshObject = { refreshToken };
    $logout.mutate(refreshObject, {
      onSuccess: (data) => {
        setCheckAuth(null);
        setUserInfo(undefined);
        deleteRefreshToken();
        navigate("/");
      },
    });
  };
  const { t, i18n } = useTranslation();
  const changeLanguage = (lng: string) => {
    i18n.changeLanguage(lng);
  };
  const pages = {
    user: [
      { name: t("header.main"), path: "/main" },
      { name: t("header.myTransactions"), path: "/main/transactions" },
    ],
    admin: [
      { name: t("header.allTransactions"), path: "/admin/transactions" },
      { name: t("header.allUsers"), path: "/admin/users" },
    ],
  };

  const settings = [{ name: t("header.logout"), function: logoutFucn }];
  const [anchorElNav, setAnchorElNav] = React.useState<null | HTMLElement>(
    null
  );
  const [anchorElUser, setAnchorElUser] = React.useState<null | HTMLElement>(
    null
  );

  const handleOpenNavMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  const navigationFunction = (path: string) => {
    // const pathEdit = window.location.pathname.split("/")[1];
    // if (pathEdit !== "quiz") {
    //   navigate(path);
    // } else {
    //   setOpen(true);
    // }
    navigate(path);
  };

  return (
    <>
      <AppBar position="static" sx={{ border: "none" }}>
        <Container maxWidth="xl">
          <Toolbar disableGutters>
            <Typography
              variant="h6"
              noWrap
              component="a"
              href="/"
              sx={{
                mr: 2,
                display: { xs: "none", md: "flex" },
                fontFamily: "monospace",
                fontWeight: 700,
                letterSpacing: ".3rem",
                color: "inherit",
                textDecoration: "none",
              }}
            >
              ITHANKU
            </Typography>
            <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
              <IconButton
                size="large"
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleOpenNavMenu}
                color="inherit"
              >
                <MenuIcon />
              </IconButton>

              <Menu
                id="menu-appbar"
                anchorEl={anchorElNav}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "left",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "left",
                }}
                open={Boolean(anchorElNav)}
                onClose={handleCloseNavMenu}
                sx={{
                  display: { xs: "block", md: "none" },
                }}
              >
                {userInfo ? (
                  pages[`${userInfo.role as "user" | "admin"}`].map((page) => (
                    <MenuItem
                      key={page.name}
                      onClick={() => navigationFunction(page.path)}
                    >
                      <Typography textAlign="center">{page.name}</Typography>
                    </MenuItem>
                  ))
                ) : (
                  <MenuItem onClick={() => navigationFunction("/")}>
                    <Typography textAlign="center">
                      {t("header.main")}
                    </Typography>
                  </MenuItem>
                )}
              </Menu>
            </Box>

            <Typography
              variant="h5"
              noWrap
              component="a"
              href=""
              sx={{
                mr: 2,
                display: { xs: "flex", md: "none" },
                flexGrow: 1,
                fontFamily: "monospace",
                fontWeight: 700,
                letterSpacing: ".3rem",
                color: "inherit",
                textDecoration: "none",
              }}
            >
              ITU
            </Typography>

            <Box
              sx={{ flexGrow: 1, display: { xs: "none", md: "flex", gap: 10 } }}
            >
              {userInfo ? (
                pages[`${userInfo.role as "user" | "admin"}`].map((page) => (
                  <Button
                    key={page.name}
                    onClick={() => navigationFunction(page.path)}
                    variant={
                      page.path === window.location.pathname
                        ? "outlined"
                        : "text"
                    }
                    color="secondary"
                    sx={{ my: 2, color: "white", display: "block" }}
                  >
                    {page.name}
                  </Button>
                ))
              ) : (
                <Button
                  onClick={() => navigationFunction("/")}
                  variant={
                    "/" === window.location.pathname ? "outlined" : "text"
                  }
                  color="secondary"
                  sx={{ my: 2, color: "white", display: "block" }}
                >
                  {t("header.main")}
                </Button>
              )}
            </Box>
            <Box mr={checkAuth ? 2 : 0}>
              <Stack>
                <ButtonGroup size="small">
                  <Button
                    color="secondary"
                    variant={
                      localStorage.getItem("i18nextLng") === "en"
                        ? "contained"
                        : "outlined"
                    }
                    onClick={(e) => changeLanguage("en")}
                  >
                    EN
                  </Button>
                  <Button
                    color="secondary"
                    variant={
                      localStorage.getItem("i18nextLng") === "ru"
                        ? "contained"
                        : "outlined"
                    }
                    onClick={(e) => changeLanguage("ru")}
                  >
                    RU
                  </Button>
                </ButtonGroup>
              </Stack>
            </Box>
            {checkAuth && userInfo && (
              <Box sx={{ flexGrow: 0 }}>
                <Stack direction="row" alignItems="center" gap={1}>
                  <Tooltip title={userInfo.email}>
                    <Typography
                      sx={{
                        cursor: "default",
                        display: {
                          lg: "block",
                          md: "block",
                          sm: "none",
                          xs: "none",
                        },
                      }}
                    >
                      {userInfo.name}
                    </Typography>
                  </Tooltip>

                  <Tooltip title={t("header.openSettings")}>
                    <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                      <Avatar
                        imgProps={{ referrerPolicy: "no-referrer" }}
                        alt="Avatar"
                        src={userInfo.picture}
                      />
                    </IconButton>
                  </Tooltip>
                </Stack>

                <Menu
                  sx={{ mt: "45px" }}
                  id="menu-appbar"
                  anchorEl={anchorElUser}
                  anchorOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: "top",
                    horizontal: "right",
                  }}
                  open={Boolean(anchorElUser)}
                  onClose={handleCloseUserMenu}
                >
                  {settings.map((setting) => (
                    <MenuItem key={setting.name} onClick={setting.function}>
                      <Typography textAlign="center">{setting.name}</Typography>
                    </MenuItem>
                  ))}
                </Menu>
              </Box>
            )}
          </Toolbar>
        </Container>
      </AppBar>
    </>
  );
};
