import { ComponentType } from "react";
import lazy, { PreloadableComponent } from "react-lazy-with-preload";

export type LazyRouteProps = {
  title: string;
  route: string;
  component: PreloadableComponent<ComponentType<any>>;
};

export type Route = LazyRouteProps;

export const auth: Array<Route> = [
  {
    title: "Main",
    route: "/main",
    component: lazy(() => import("../../pages/main")),
  },
  {
    title: "My transactions",
    route: "/main/transactions",
    component: lazy(() => import("../../pages/main/userTransactions")),
  },
  {
    title: "My QR code",
    route: "/main/qr",
    component: lazy(() => import("../../pages/main/generateQR")),
  },
  {
    title: "Reade QR code",
    route: "/main/readeqr",
    component: lazy(() => import("../../pages/main/readeQR")),
  },
  {
    title: "Donate money",
    route: "/main/donate/:userId",
    component: lazy(() => import("../../pages/main/donate")),
  },
];
export const mutual: Array<Route> = [];
export const unAuth: Array<Route> = [
  {
    title: "Login",
    route: "/",
    component: lazy(() => import("../../pages/login")),
  },
  {
    title: "Logined successfuly",
    route: "/login/success",
    component: lazy(() => import("../../pages/login/success")),
  },
  {
    title: "Create new account",
    route: "/signup",
    component: lazy(() => import("../../pages/registration")),
  },
];

export const admin: Array<Route> = [
  {
    title: "All transactions",
    route: "/admin/transactions",
    component: lazy(() => import("../../pages/admin/transactions")),
  },
  {
    title: "All users",
    route: "/admin/users",
    component: lazy(() => import("../../pages/admin/users")),
  },
];
export const superAdmin: Array<Route> = [];
export const routes = [...auth, ...unAuth, ...mutual, ...admin, ...superAdmin];
